.header {
    height: 34vw;
    margin: 30px auto;
    background: url('../../../public/header_img.png') no-repeat;
    background-size: contain;
    position: relative;
}
.header-contents {
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: start;
    gap: 1.5vh ;
    max-width: 50%;
    bottom: 10%;
    left: 6vw;
    animation: fadeIn 3s;
}

.header-contents h2 {
    font-weight: 500;
    color: white;
    font-size: max(4.5vw, 22px);
}
.header-contents p {
    color: white;
    font-size: 1vw;
}
.header button {
    background: white;
    font-weight: 500;
    border: none;
    color: #747474;
    padding: 1vw 2.3vw;
    border-radius: 50px;
    font-size: max(1vw, 13px);

}

@media (max-width: 1050px) {
    .header-contents {
        max-width: 45%;
    }
}
@media (max-width: 750px) {
    .header-contents {
        max-width: 56%;
    }
    .header-contents p {
        display: none;
    }
    .header-contents button {
        padding: 2vw 4vw;
    }
}